<template>
  <div class="parent">
    <mu-row class="child" gutter>
      <mu-col width="5" tablet="25" desktop="25"></mu-col>
      <mu-col width="90" tablet="50" desktop="50">
        <mu-card>
          <mu-text-field label="用户名" hintext="用户名最长32个字符" icon="account_circle" type="text" :errorText="error_username" v-model="username" labelFloat/><br/>
          <mu-text-field label="邮箱" hintText="用于登录" icon="mail" type="email" :errorText="error_email" v-model="email" labelFloat/><br/>
          <mu-text-field label="密码" hintText="密码8-20字符" icon="lock" type="password" :errorText="error_password" v-model="password" labelFloat/><br/>
          <mu-text-field label="地址" hintText="最长64个字符" icon="place" type="text" v-model="location" labelFloat/><br/>
          <mu-text-field label="简介" icon="perm_contact_calendar" type="text" multiLine :rows="3" :rowsMax="6" v-model="about_me" labelFloat/><br/>
          <mu-card-actions>
            <mu-raised-button label="注册" v-on:click="register" primary/>
          </mu-card-actions>
        </mu-card>
      </mu-col>
      <mu-col width="5" tablet="25" desktop="25"></mu-col>
    </mu-row>
  </div>
</template>

<script>
  import api from '../services/api'
  export default {
    data: function () {
      return {
        username: '',
        email: '',
        location: '',
        password: '',
        about_me: '',
        error_username: '',
        error_email: '',
        error_password: ''
      }
    },
    methods: {
      register: function () {
        let flag = true
        if (!this.username) {
          this.error_username = '用户名不能为空'
          flag = false
        }
        if (!this.email) {
          this.error_email = '邮箱不能为空'
          flag = false
        }
        if (!this.password) {
          this.error_password = '密码不能为空'
          flag = false
        }
        if (!flag) return
        this.$http.post(
          api.user,
          {
            email: this.email,
            password: this.password,
            username: this.username,
            location: this.location,
            about_me: this.about_me
          }
        ).then((response) => {
          this.$cookie.set(
            'token',
            response.data.token,
            {expires: response.data.expiration + 's'}
          )
          this.$router.push({name: 'index'})
        }, (response) => {
          alert('fail')
        })
      }
    }
  }
</script>

<style scoped>
  .parent {
    /*width: 50%;*/
    text-align: center;
    margin-top: 8px;
  }
</style>



